body {
  font-family: "Fira Sans Extra Condensed"; }

.hero {
  min-width: 100%;
  background-image: url("/src/img/back1.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: white;
  padding: 50px;
  margin-top: 98px; }
  .hero .text-raws {
    max-width: 1200px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    margin: 0 auto; }
  .hero .text-raw_1 {
    font-weight: 700;
    font-size: 120px; }
  .hero .text-raw_2 {
    align-self: center;
    font-weight: 500;
    font-size: 64px; }
  .hero .text-raw_3 {
    align-self: flex-end;
    font-weight: 400;
    font-size: 48px; }

@media (max-width: 1024px) {
  .hero .text-raw_1 {
    font-size: 64px; }
  .hero .text-raw_2 {
    font-size: 48px; }
  .hero .text-raw_3 {
    font-size: 24px; } }

@media (max-width: 530px) {
  .hero {
    padding: 50px 10px; }
    .hero .text-raw_1 {
      align-self: center;
      font-size: 48px; }
    .hero .text-raw_2 {
      align-self: center;
      font-size: 24px; }
    .hero .text-raw_3 {
      align-self: center;
      font-size: 18px; } }

